if ('.carousel') {
  const swiperCarousel = new Swiper(".carousel", {
    grabCursor: true,
    loop: true,
    // slidesPerView: 1,
    // spaceBetween: 30,
    centeredSlides: true,

    autoplay: {
      delay: 5000, // время задержки между слайдами
      disableOnInteraction: false, // слайды будут продолжать автоматически переключаться при нажатии на кнопки навигации
    },

    keyboard: {
      enabled: true,
      onlyInViewport: true,
      pageUpDown: true,
    },
    // Navigation arrows
    navigation: {
      nextEl: '.swiper-button-next',
      prevEl: '.swiper-button-prev',
    },

    breakpoints: {
      320: {
        slidesPerView: 1,
        spaceBetween: 20,
      },
      460: {
        slidesPerView: 1.5,
        spaceBetween: 20,
      },
      // 660: {
      //   slidesPerView: 1.5,
      //   spaceBetween: 20,
      // },
      768: {
        slidesPerView: 2,
        spaceBetween: 40,
      },
      1360: {
        slidesPerView: 3,
        spaceBetween: 30,
      },
    },
  });
}
